import React, { useEffect, useState } from "react";
import { useHistory, useParams } from "react-router-dom";
import "./Detail.scss";

function Detail(props) {
	useEffect(() => {
		let timer = setTimeout(() => {
			changeAlert(false);
		}, 2000);
		return () => {
			clearTimeout(timer);
		};
	}, []);

	let [alert, changeAlert] = useState(true);
	let history = useHistory();
	let { id } = useParams();
	let findItem = props.shoes.find((item) => {
		return item.id == id;
	});

	return (
		<div>
			<div className="container">
				<div className="red">
					<h4 className="red-p">Detail</h4>
				</div>
				{alert === true ? (
					<div className="alert">
						<p>Almost sold out!!</p>
					</div>
				) : null}

				<div className="row">
					<div className="col-md-6">
						<img
							src={
								"https://codingapple1.github.io/shop/shoes" +
								(findItem.id + 1) +
								".jpg"
							}
							width="100%"
						/>
					</div>
					<div className="col-md-6 mt-4">
						<h4 className="pt-5">{findItem.title}</h4>
						<p>{findItem.content}</p>
						<p>{findItem.price}</p>

						<Stocks stock={props.stock} />

						<button
							className="btn btn-danger"
							onClick={() => {
								props.changeStock([9, 10, 11]);
							}}
						>
							주문하기
						</button>
						<button
							className="btn btn-danger"
							onClick={() => {
								history.goBack();
							}}
						>
							뒤로가기
						</button>
					</div>
				</div>
			</div>
		</div>
	);
}

function Stocks(props) {
	return <p>Quantity: {props.stock[0]}</p>;
}

export default Detail;
